import React, { useState, useEffect } from "react";
import { useCookies } from "react-cookie";

const Cart = () => {
  const [cookies, setCookie] = useCookies(["name"]);
  const [products, setProduct] = useState([]);

  useEffect(() => {
    setProduct(cookies.products);
  }, [cookies.products]);

  return (
    <div className="sideList-cart">
      <h5>Shopping Cart</h5>
      <div className="cart-content">
        <div className="cart-items">
          {products &&
            products.map((product, index) => (
              <div className="item" key={index}>
                <div className="img">
                  <img
                    src={`/images/products/${product.name}1.jpeg`}
                    alt="product"
                  />
                </div>
                <div className="cart-title">
                  <a href={`/product-detail/${product.name}`}>{product.name}</a>
                  <p className="price">
                    {product.unit} x ${product.price}
                  </p>
                </div>
                <div
                  className="cart-delete"
                  onClick={() => {
                    setCookie("products", [
                      ...products.slice(0, index),
                      ...products.slice(index + 1),
                    ]);
                    setProduct([
                      ...products.slice(0, index),
                      ...products.slice(index + 1),
                    ]);
                  }}
                >
                  x
                </div>
              </div>
            ))}
        </div>
        <h6 className="mb-30">
          Subtotal:{" "}
          <span className="cart-amount">
            $
            {products &&
              products.reduce(
                (accum, curr) => accum + curr.price * curr.unit,
                0
              )}
          </span>
        </h6>
        <div className="cart-checkout-btn">
          <a
            href="/"
            onClick={() => {
              setCookie("products", [
                {
                  name: "Hand White Lace Skater Dress",
                  price: 49,
                  unit: 1,
                },
                {
                  name: "Bella Two-Piece Block Heel Sandals",
                  price: 29,
                  unit: 1,
                },
              ]);
            }}
          >
            View Cart
          </a>
        </div>
        <div className="cart-checkout-btn">
          <a href="/">Checkout</a>
        </div>
      </div>
    </div>
  );
};

export default Cart;
